import Vue from 'vue';
import axios from 'axios';

import { Loading, Message } from 'element-ui';

// 
import router from '@/router';

let loading;
// baseURLapi${baseURL}/api/path
// axios.defaults.baseURL = 'http://localhost:3003/';
// 
axios.interceptors.request.use(function(config) {
	// 
	loading = Loading.service({ background: 'rgba(0, 0, 0, 0.8)' });
	// api,token
	if (config.url == '/api/admin/login' || config.url == '/api/admin/register') {
		return config;
	}
	if (!sessionStorage.token) {
		Message.error({
			message: 'token',
			onClose: () => {
				router.replace('/login');
				loading.close();
			}
		});
	}
	config.headers.Authorization = `Bearer ${sessionStorage.token}`;
	return config;
}, function(error) {
	// 
	return Promise.reject(error);
});

// 
axios.interceptors.response.use(function({ status, data, response }) {
	//
	loading.close();
	switch (status) {
		case 200:
			return data;
			break;
		default:
			Message.error(response.statusText);
			break;
	}
}, function(error) {
	// 401
	if (error.response.status == 401) {
		let expiredTime = new Date(error.response.data.inner.expiredAt).toLocaleString();
		Message.error({
			message: `Token,${expiredTime}`,
			onClose: () => {
				router.replace('/login');
				loading.close();
			}
		});
	}
	// 
	return Promise.reject(error);
});
