<template>
	<div>
		<el-card class="box-card">
			<div slot="header" class="clearfix">
				<span></span>
			</div>
			<el-alert title="Tips" type="warning" description="" show-icon :closable="false">
			</el-alert>
			<!--  -->
			<el-tree class="am-margin-top-lg" ref="tree" :default-expanded-keys="[1]" lazy node-key="id" :load="loadNode" :props="defaultProps">
				<div class="node-box" slot-scope="{ node, data }">
					<div class="node-name">
						{{ node.label }}
					</div>
					<div class="node-action">
						<el-popover placement="left" class="am-margin-right-sm" trigger="hover">
							<i v-if="data.icon" class="am-text-xxl" :class="`el-icon-${data.icon}`"></i>
							<div v-else class="am-text-xxl"></div>
							<el-button slot="reference" type="text" :icon="data.icon?`el-icon-${data.icon}`:''" :disabled="data.id==1"
							 @click.stop="openIconModal(node,data)">
							</el-button>
						</el-popover>
						<el-button type="text" icon="el-icon-edit-outline" :disabled="data.id==1" @click.stop="openEditModal(node,data)">
						</el-button>
						<el-button type="text" icon="el-icon-plus" @click.stop="openAddModal(node,data)">
						</el-button>
						<el-button type="text" icon="el-icon-delete" :disabled="data.id==1" @click.stop="openDeleteModal(node,data)">
						</el-button>
						<div class="node-order am-margin-left-sm">
							<el-tag size="mini">{{ data.order }}</el-tag>
						</div>
					</div>
				</div>
			</el-tree>
			<!-- Modal -->
			<el-dialog title="" :visible.sync="EditModalVisible">
				<el-form label-width="80px" :label-position="'left'">
					<el-form-item label="">
						<el-input v-model="editForm.name"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input v-model="editForm.component" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input v-model="editForm.path" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input v-model="editForm.order" placeholder="2001"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click="EditModalVisible = false"> </el-button>
					<el-button type="primary" @click="UpdateNodeHandle"> </el-button>
				</div>
			</el-dialog>
			<!-- Modal -->
			<el-dialog title="" width="72%" :visible.sync="IconModalVisible">
				<!--  -->
				<ul class="icon-list">
					<li v-for="item in icons" :key="item.id" :class="{checked:item.checked}" @click="checkIcon(item)">
						<span>
							<i :class="`el-icon-${item.name}`"></i>
							<span class="icon-name">el-icon-{{item.name}}</span>
						</span>
					</li>
				</ul>
				<!--  -->
				<el-pagination background layout="->,prev, pager, next" :page-size="pageSize" :total="iconTotal" @current-change="currentPageChange"></el-pagination>

				<div slot="footer" class="dialog-footer">
					<el-button @click="IconModalVisible = false"> </el-button>
					<el-button type="primary" @click="UpdateIconHandle"> </el-button>
				</div>
			</el-dialog>
			<!-- Modal -->
			<el-dialog title="" :visible.sync="AddModalVisible">
				<el-form label-width="80px" :label-position="'left'">
					<el-form-item label="">
						<el-input v-model="addForm.name"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input v-model="addForm.component" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input v-model="addForm.path" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input v-model="addForm.order" placeholder="2001"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click="AddModalVisible = false"> </el-button>
					<el-button type="primary" @click="AddNodeHandle"> </el-button>
				</div>
			</el-dialog>
		</el-card>
	</div>
</template>

<script>
	import { Menu, Icon } from "@/api/index";

	export default {
		name: "Menu",
		data() {
			return {
				defaultProps: {
					label: 'name'
				},
				// 
				IconModalVisible: false,
				icons: [],
				pageSize: 30,
				iconTotal: 0,
				checkedIcon: '',
				checkedIconName: '',
				// Modal
				EditModalVisible: false,
				editForm: {
					name: "",
					pId: '',
					component:"",
					path: '',
					order: '',
				},
				// Modal
				AddModalVisible: false,
				addForm: {
					name: "",
					pId: '',
					component:"",
					path: '',
					order: '',
				},
				currentNodeData: '',
				currentNode: '',
			}
		},
		created() {
			this.loadIcons(1);
			document.title = "";
		},
		methods: {
			async loadNode(node, resolve) {
				// level==0
				if (node.level === 0) {
					let { data } = await Menu.loadSub({ pId: 0 });
					return resolve(data);
				}
				let { data } = await Menu.loadSub({ pId: node.data.id });
				resolve(data);
			},
			// element
			async loadIcons(pageIndex) {
				let { status, icons, total } = await Icon.list({ pageSize: this.pageSize, pageIndex });
				if (status) {
					icons.forEach((item) => {
						// --
						item.checked = false;
					});
					this.icons = icons;
					this.iconTotal = total;
				}
			},
			// Modal
			openIconModal(node, data) {
				this.IconModalVisible = true;
				// data
				this.currentNodeData = data;
			},
			// 
			currentPageChange(pageIndex) {
				this.loadIcons(pageIndex);
			},
			// 
			checkIcon(data) {
				this.icons.forEach((item) => {
					// 
					item.checked = item == data ? !data.checked : false;
				});
				// /iconName
				this.checkedIcon = data.checked ? data.id : '';
				this.checkedIconName = data.checked ? data.name : '';
			},
			// 
			async UpdateIconHandle() {
				let { status, msg } = await Menu.setIcon({ id: this.currentNodeData.id, icon: this.checkedIcon });
				if (status) {
					this.$message.success(msg);
					this.IconModalVisible = false;
					// 
					this.currentNodeData.icon = this.checkedIconName;
				}
			},
			// Modal
			openEditModal(node, data) {
				this.EditModalVisible = true;
				this.editForm = { ...data };
				// node
				this.currentNode = node;
			},
			// ModalpId
			openAddModal(node, data) {
				this.addForm.pId = data.id;
				this.AddModalVisible = true;
				// data
				this.currentNode = node;
			},
			// 
			openDeleteModal(node, data) {
				this.$confirm(', ?', {
						type: 'warning',
					})
					.then(async () => {
						let { status } = await Menu.remove({ id: data.id });
						if (status) {
							node.remove();
							this.$message.success('!');
						}
					})
					.catch(() => {
						this.$message.info('');
					});
			},
			// 
			async AddNodeHandle() {
				let { status, msg, data } = await Menu.add({ ...this.addForm });
				if (!status) {
					this.$message.error(msg);
					return false;
				}
				this.$message.success(msg);
				this.AddModalVisible = false;
				// 
				this.$refs.tree.append({ ...this.addForm, ...data }, this.currentNode);
				//
				this.addForm = { name: "", pId: '', path: '' };
			},
			// 
			async UpdateNodeHandle() {
				let { status, msg } = await Menu.update({ ...this.editForm });
				if (status) {
					this.$message.success(msg);
					this.EditModalVisible = false;
					// 
					this.currentNode.data = { ...this.editForm }
				}
			},
		}
	}
</script>

<style lang="less" scoped>
	.node-box {
		display: flex;
		flex: 1;
		align-items: center;
		justify-content: space-between;
		font-size: 14px;

		.node-order {
			width: 100px;
			display: inline-block;
			font-size: 12px;
		}
	}

	ul.icon-list {
		overflow: hidden;
		list-style: none;
		padding: 0 !important;
		border: solid 1px #eaeefb;
		border-radius: 4px;
	}

	.icon-list li {
		float: left;
		width: 10%;
		text-align: center;
		height: 100px;
		line-height: 100px;
		color: #666;
		font-size: 10px;
		border-right: 1px solid #eee;
		border-bottom: 1px solid #eee;
		margin-right: -1px;
		margin-bottom: -1px;

		&::after {
			display: inline-block;
			content: "";
			height: 100%;
			vertical-align: middle;
		}

		span {
			display: inline-block;
			line-height: normal;
			vertical-align: middle;
			font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
				"Microsoft YaHei", SimSun, sans-serif;
			color: #99a9bf;
			transition: color 0.15s linear;
		}

		i {
			display: block;
			font-size: 26px;
			margin-bottom: 15px;
			color: #606266;
			transition: color 0.15s linear;
		}

		.icon-name {
			display: inline-block;
			padding: 0 3px;
			height: 1em;
		}

		&.checked,
		&.checked:hover {
			background-color: rgb(92, 182, 255);

			span,
			i {
				color: white;
			}
		}

		&:hover {

			span,
			i {
				color: rgb(92, 182, 255);
			}
		}
	}
</style>
