<template>
	<div>
		<el-card class="box-card">
			<div slot="header" class="clearfix">
				<span></span>
			</div>
			<el-table :data="tableData" style="width: 100%">
				<el-table-column prop="id" label="#">
				</el-table-column>
				<el-table-column sortable label="">
					<template slot-scope="scope">
						<el-avatar :src="scope.row.avatar" :size="50"></el-avatar>
					</template>
				</el-table-column>
				<el-table-column prop="username" sortable label="">
				</el-table-column>
				<el-table-column prop="fullname" sortable label="">
				</el-table-column>
				<el-table-column prop="sex" sortable label="">
				</el-table-column>
				<el-table-column prop="tel" sortable label="">
				</el-table-column>
				<el-table-column prop="role_name" sortable label="">
					<template slot-scope="scope">
						<el-tag :type="scope.row.role===1?'danger':''">{{scope.row.role_name}}</el-tag>
					</template>
				</el-table-column>
				<el-table-column prop="login_time" width="160" sortable label="">
				</el-table-column>
				<el-table-column prop="login_count" sortable label="">
				</el-table-column>
				<el-table-column label="">
					<template slot-scope="scope">
						<el-button @click="showEditModal(scope.row)" plain icon="el-icon-edit" size="small" type="primary"></el-button>
						<el-button @click="showDeleteModal(scope.row.id)" :disabled="scope.row.id==1" icon="el-icon-delete" plain size="small"
						 type="danger"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-card>
		<!---->
		<el-dialog title="" :visible.sync="editModalShow" @closed="handleCloseDialog('form')">
			<el-form ref="form" :model="form" :rules="rules" label-position="left" label-width="80px">
				<el-form-item label="" prop="fullname">
					<el-input v-model="form.fullname" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="sex">
					<el-radio-group v-model="form.sex">
						<el-radio label=""></el-radio>
						<el-radio label=""></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="tel">
					<el-input v-model="form.tel" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="role">
					<el-select v-model="form.role" placeholder="">
						<el-option v-for="item in roles" :key="item.id" :label="item.name" :value="item.id"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="avatar">
					<single-upload default-image="/images/avatar/default.jpg" :data="{type:'avatar'}" action="/api/upload/common/" :url.sync="form.avatar" />
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="editModalShow = false"> </el-button>
				<el-button type="primary" @click="updateInfo"> </el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	//service
	import { Admin, Role } from '@/api/index'
	import SingleUpload from '@/components/SingleUpload.vue';

	export default {
		name: "List",
		components: {
			SingleUpload,
		},
		data() {

			return {
				tableData: [],
				roles: [],
				editModalShow: false,
				form: {
					id: "",
					fullname: "",
					sex: "",
					tel: "",
					role: "",
					avatar: "",
				},
				rules: {
					fullname: [
						{ required: true, type: 'string', message: '', trigger: 'blur' },
						{ pattern: /^[\u4E00-\u9FA5A-Za-z\s]+([\u4E00-\u9FA5A-Za-z]+)*$/, message: '', trigger: 'blur' }
					],
					sex: [
						{ required: true, message: '', trigger: 'blur' },
					],
					tel: [
						{ required: true, message: '', trigger: 'blur' },
						{ pattern: /^1(3|4|5|6|7|8|9)\d{9}$/, message: '', trigger: 'blur' }
					],
					role: [
						{ required: true, message: '', trigger: 'blur' },
					],
					avatar: [
						{ required: true, message: '', trigger: 'click' },
					],
				}
			};
		},
		created() {
			this.loadList();
			this.loadRole();
			document.title = "";
		},
		methods: {
			async loadList() {
				let { status, data } = await Admin.list();
				if (status) {
					this.tableData = data;
				}
			},
			async loadRole() {
				let { status, data } = await Role.list();
				if (status) {
					this.roles = data;
				}
			},
			// 
			showEditModal(row) {
				this.editModalShow = true;
				this.form = { ...row };
			},
			// 
			showDeleteModal(id) {
				this.$confirm(', ?', { type: 'warning' })
					.then(async () => {
						let { status, msg } = await Admin.remove({ id });
						if (status) {
							this.$message.success('');
							this.loadList();
						}
					})
					.catch(() => {
						this.$message.info('');
					});
			},
			// 
			updateInfo() {
				this.$refs.form.validate(async (valid) => {
					if (!valid) { return false };
					let { status, msg } = await Admin.update({ ...this.form });
					if (status) {
						this.editModalShow = false;
						this.$message.success(msg);
						this.loadList();
					}
				})
			},
			// dialog
			handleCloseDialog(formName) {
				this.$refs[formName].clearValidate();
			},
		}
	};
</script>

<style scoped>

</style>
