import axios from 'axios';

// 
const list = (formData) => axios.get('/api/admin/list', { params: formData });
// 
const update = (formData) => axios.put('/api/admin', formData);
// 
const remove = (formData) => axios.delete('/api/admin', { params: formData });

export default {
	list,
	update,
	remove
}
