<template>
	<div>
		<el-card class="box-card">
			<div slot="header" class="clearfix">
				<span></span>
			</div>
			<!--  -->
			<el-table :data="tableData" style="width:100%">
				<el-table-column fixed label="" width="550">
					<template slot-scope="scope">
						<div class="goods">
							<div class="photo">
								<img :src="scope.row.img_md" />
							</div>
							<div class="name">
								<span>{{scope.row.name}}</span>
								<br />
								<span>{{scope.row.articleNo}}</span>
							</div>
						</div>
					</template>
				</el-table-column>
				<el-table-column sortable prop="price" label="">
				</el-table-column>
				<el-table-column sortable prop="inventory" label="">
				</el-table-column>
				<el-table-column sortable prop="create_time" label="">
				</el-table-column>
				<el-table-column label="">
					<template slot-scope="scope">
						<router-link class="am-margin-right-sm" :to="{ name: 'GoodsEdit', params: { id: scope.row.id }}">
							<el-button type="primary" plain icon="el-icon-edit" size="small"></el-button>
						</router-link>
						<el-button @click="showDeleteModal(scope.row.id)" icon="el-icon-delete" plain type="danger"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!--  -->
			<el-pagination :page-sizes="[4,8,16,32]" :page-size="pageSize" :total="total" @size-change="pageSizeChange"
			 @current-change="currentPageChange" background layout="->,prev,pager,next,sizes,total" class="am-margin-top-sm"></el-pagination>
		</el-card>
	</div>
</template>
<script>
	import { Goods } from '@/api/index';

	export default {
		data() {
			return {
				tableData: [],
				total: 0,
				pageSize: 4,
				pageIndex: 1,
			};
		},
		created() {
			this.loadGoodsList();
			document.title = "";
		},
		methods: {
			async loadGoodsList() {
				let { status, goods, total } = await Goods.list({
					pageIndex: this.pageIndex,
					pageSize: this.pageSize,
				});
				if (status) {
					this.tableData = goods;
					this.total = total;
				}
			},
			// 
			currentPageChange(pageIndex) {
				this.pageIndex = pageIndex;
				this.loadGoodsList();
			},
			// pageSize
			pageSizeChange(size) {
				this.pageSize = size;
				this.loadGoodsList();
			},
			async showDeleteModal(id) {
				this.$confirm('', {
						type: 'warning'
					})
					.then(async () => {
						let { status, msg } = await Goods.remove({ id });
						if (status) {
							this.$message.success('');
							this.loadGoodsList();
						}
					})
					.catch(() => {
						this.$message.info('');
					});
			},
		}
	};
</script>
<style lang="less" scoped>
	.goods {
		display: flex;
		align-items: center;

		.photo {
			margin-right: 6px;
			border: 1px solid #eee;
		}

		.photo img {
			width: 80px;
			display: block;
		}
	}
</style>
