<template>
	<div>
		<el-card class="am-margin-bottom-sm" v-for="(item,index) in menu" :key="item.id">
			<div slot="header" class="menu-header">
				<div class="menu-title am-text-sm">
					<i class="el-icon-collection-tag"></i>
					{{item.name}}
				</div>
				<el-switch @change="parentHandle(item)" v-model="item.checked"></el-switch>
			</div>
			<el-row :gutter="40">
				<el-col v-for="element in item.children" :key="element.id">
					<div class="menu-box am-padding-vertical-xs">
						<div class="am-text-xs">{{element.name}}</div>
						<el-switch @change="childHandle(element,item)" v-model="element.checked"></el-switch>
					</div>
				</el-col>
			</el-row>
		</el-card>
	</div>
</template>

<script>
	import { Role } from "@/api/index";

	export default {
		name: "Role-Setting",
		props: ["id"],
		data() {
			return {
				menu: [],
			};
		},
		created() {
			this.loadRoleConfig();
			document.title = "";
		},
		watch: {
			// 
			'$route': 'loadRoleConfig'
		},
		methods: {
			async loadRoleConfig() {
				let { status, data } = await Role.loadConfig({ id: this.id });
				if (status) {
					this.menu = data;
				}
			},
			/*
			 * switch
			 * nodeData      
			 * */
			async parentHandle({ checked, children, id }) {
				//
				if (checked) {
					let { status } = await Role.createMenu({ role_id: this.id, menu_id: id });
				} else {
					let { status } = await Role.removeMenu({ role_id: this.id, menu_id: id });
				}
				//
				children.forEach(async (item) => {
					if (checked) {
						let { status } = await Role.createMenu({ role_id: this.id, menu_id: item.id });
					} else {
						let { status } = await Role.removeMenu({ role_id: this.id, menu_id: item.id });
					}
					item.checked = checked;
				});
			},
			/*
			 * switch,
			 * nodeData    data
			 * parentData  data
			 * */
			async childHandle({ checked, id }, parentData) {
				//
				let num = parentData.children.reduce((total, item) => {
					if (item.checked) {
						total++;
					}
					return total;
				}, 0);
				// 
				if (checked) {
					let { status } = await Role.createMenu({ role_id: this.id, menu_id: id });
				} else {
					let { status } = await Role.removeMenu({ role_id: this.id, menu_id: id });
					// truefalse,1ajax
					if (num == 1) return;
				}
				switch (num) {
					case 0:
						await Role.removeMenu({ role_id: this.id, menu_id: parentData.id });
						parentData.checked = false;
						break;
					case 1:
						await Role.createMenu({ role_id: this.id, menu_id: parentData.id });
						parentData.checked = true;
						break;
					default:
						break;
				}
			},
		},
	}
</script>

<style lang="less" scoped>
	.menu-header {
		display: flex;
		justify-content: space-between;
		align-items: center;

		.menu-title {
			color: #409EFF;
		}
	}

	.menu-box {
		display: flex;
		justify-content: space-between;
		align-items: center;
		color: #909399;
	}
</style>
