<template>
	<div class="bg">
		<el-card shadow="always" class="box-card form-box">
			<div slot="header">
				<span></span>
			</div>
			<el-form ref="form" :rules="rules" size="medium" :model="form" label-width="60px">
				<el-form-item prop="username" label="">
					<el-input placeholder=" 3  15 !" prefix-icon="el-icon-user" v-model="form.username"></el-input>
				</el-form-item>
				<el-form-item prop="password" label="">
					<el-input placeholder="3!" prefix-icon="el-icon-key" type="password" v-model="form.password"></el-input>
				</el-form-item>
				<el-form-item prop="fullname" label="">
					<el-input placeholder="" prefix-icon="el-icon-postcard" v-model="form.fullname"></el-input>
				</el-form-item>
				<el-form-item prop="sex" label="">
					<el-radio-group v-model="form.sex">
						<el-radio label=""></el-radio>
						<el-radio label=""></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item prop="tel" label="">
					<el-input placeholder="!" prefix-icon="el-icon-mobile-phone" v-model="form.tel"></el-input>
				</el-form-item>
				<el-form-item prop="isAgree">
					<el-checkbox v-model="form.isAgree" label=""></el-checkbox>
				</el-form-item>
				<el-form-item>
					<el-button @click="registerHandle" type="primary"> </el-button>
				</el-form-item>
			</el-form>
			<div class="link-box">
				<router-link to="/login"></router-link>
				<router-link to="/"></router-link>
			</div>
		</el-card>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					username: '',
					password: '',
					fullname: '',
					sex: '',
					tel: '',
					isAgree: [],
				},
				rules: {
					username: [
						{ required: true, message: '', trigger: 'blur' },
						{ type: 'string', min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' }
					],
					password: [
						{ required: true, message: '', trigger: 'blur' },
						{ type: 'string', pattern: /^[0-9]{3,}$/, message: '3', trigger: 'blur' },
					],
					fullname: [
						{ required: true, message: '', trigger: 'blur' },
						{ type: 'string', pattern: /^[\u4E00-\u9FA5A-Za-z\s]+([\u4E00-\u9FA5A-Za-z]+)*$/, message: '', trigger: 'blur' }
					],
					tel: [
						{ required: true, message: '', trigger: 'blur' },
						{ type: 'string', pattern: /^1(3|4|5|6|7|8|9)\d{9}$/, message: '', trigger: 'blur' }
					],
					isAgree: [
						{ required: true, message: '', trigger: 'change' },
					]
				}
			}
		},
		created() {
			document.title = "";
		},
		methods: {
			registerHandle() {
				// 1.
				this.$refs.form.validate((valid, obj) => {
					if (valid) {
						// 2.
						this.$store
							.dispatch('User/Register', { ...this.form })
							.then(({ msg, data, status }) => {
								// token,uid,role (1-2-3-)
								if (status) {
									sessionStorage.token = data.token;
									sessionStorage.role = data.role;
									sessionStorage.id = data.id;
									// 
									this.$message({
										message: msg,
										type: 'success',
										duration: 1000,
										onClose: () => {
											this.$router.push('/goods')
										}
									});
								} else {
									this.$message.error(msg);
								}
							})
					}
				});
			},
		}
	}
</script>

<style scoped="scoped" lang="less">
	.bg {
		width: 100vw;
		height: 100vh;
		background: url(../../assets/img/login/login-bg.jpg);
		background-size: cover;
		position: relative;

		.form-box {
			position: absolute;
			right: 100px;
			top: 50%;
			transform: translateY(-50%);
			background-color: white;
			border-radius: 4px;
			font-size: 14px;
			width: 360px;

			.link-box {
				display: flex;
				justify-content: space-between;
				align-items: center;
			}
		}
	}
</style>
