// 0. VueVueRouter Vue.use(Router)
import Vue from 'vue'
import Router from 'vue-router'
import { Menu } from "@/api/index";

Vue.use(Router)
// 1. import views
// layout
import Layout from '@/components/Layout.vue';
// 
import UserLogin from '@/views/User/Login.vue';
import UserRegister from '@/views/User/Register.vue';
import UserList from '@/views/User/List.vue';
import UserInfo from '@/views/User/Info.vue';
// 
import OrderList from '@/views/Order/List.vue';
// 
import GoodsCategory from '@/views/Goods/Category.vue';
import GoodsList from "@/views/Goods/List";
import GoodsRelease from "@/views/Goods/Release";
import GoodsEdit from "@/views/Goods/Edit";
//
import AuthRole from "@/views/Auth/Role";
import AuthMenu from "@/views/Auth/Menu";
import RoleConfig from "@/views/Auth/Role-Config";

/**
 * 2. 
 * 
 * "component"  Vue.extend() 
 * 
 */
const routes = [{
		path: '/register',
		name: 'UserRegister',
		component: UserRegister,
	},
	{
		path: '/login',
		name: 'UserLogin',
		component: UserLogin,
		props: (route) => route.query,
		alias: "/",
	},
	{
		path: '/goods',
		name: 'Goods',
		component: Layout,
		redirect: '/goods/list',
		meta: { requiredAuth: true },
		children: [{
				path: 'category',
				name: 'GoodsCategory',
				component: GoodsCategory,
			},
			{
				path: "list",
				name: "GoodsList",
				component: GoodsList,
			},
			{
				path: "release",
				name: "GoodsRelease",
				component: GoodsRelease,
			},
			{
				path: "edit/:id",
				name: "GoodsEdit",
				component: GoodsEdit,
				props: true,
			}
		]
	},
	{
		path: '/user',
		name: 'User',
		component: Layout,
		meta: { requiredAuth: true },
		children: [{
			path: "list",
			name: "UserList",
			component: UserList,
		}, {
			path: "info",
			name: "UserInfo",
			component: UserInfo,
		}, ]
	},
	{
		path: '/auth',
		name: 'Auth',
		component: Layout,
		meta: { requiredAuth: true },
		children: [{
				path: "menu",
				name: "AuthMenu",
				component: AuthMenu,
			},
			{
				path: "role",
				name: "AuthRole",
				component: AuthRole,
				children: [{
					path: "config/:id",
					name: "RoleConfig",
					component: RoleConfig,
					props: true,
				}]
			}
		]
	},
	{
		path: '/order',
		name: 'Order',
		component: Layout,
		meta: { requiredAuth: true },
		children: [{
			path: "list",
			name: "OrderList",
			component: OrderList,
		}]
	}
];

// 3.  router  `routes` 
const router = new Router({
	routes // ()  routes: routes
})
// 4.
router.beforeEach(async (to, from, next) => {
	if (to.matched.some(record => record.meta.requiredAuth)) {
		// token,
		if (!sessionStorage.token) {
			next({
				path: '/login',
				query: {
					redirect: to.fullPath
				}
			});
			return;
		}
		// 
		next();
	} else {
		next();
	}
});

export default router
